<template>

  <div class='page'>

    <input class='specialInput' type='text' placeholder='Код подтверждения' v-model='confirmationCode'/>

    <div class='enterCode' @click='sendConfirmationCode'>Ввести код подтверждения</div>

    <div class='errorBlock' v-if='errorMessage!=""'>{{errorMessage}}</div>
  
  </div>

</template>

<script>

  import { useAutorizationStore } from '../stores/autorization.js';

  import { confirmUserRegistration } from '../api/confirmUserRegistration.js';

  import { computed } from 'vue'

  export default {

    setup() {
  
      const autorizationStore=useAutorizationStore();

      const email=computed(()=>autorizationStore.email);

      const password=computed(()=>autorizationStore.password);

      return {email, password }

    },

    data(){

      return{

        confirmationCode:'',

        errorMessage:'',

      }
    },


    //При вводе пароли или логина ошибка прошлого неверного ввода обнуляется
    watch:{

      confirmationCode(){

        this.errorMessage='';

      },

    },

    methods:{

      async sendConfirmationCode(){

        let response=await confirmUserRegistration({

          email:this.email,

          password:this.password,

          confirmationCode:this.confirmationCode,

        });

        console.log(response);

       if(response.data.errorExist==true){

          if(response.data.actionMessage=='registrationOfUserHasBeenConfirmed'){

          this.errorMessage='Пользователь был уже зарегистрирован ранее';

          }else if(response.data.actionMessage=='invalidConfirmationCode'){

          this.errorMessage='Неверный код подтверждения';

          }else if(response.data.actionMessage=='serverIsNotAvailable'){

            this.registerErrorMessage='Нет доступа к серверу';

          }

        }

      },

    }

  }

</script>

<style  scoped src="@/shared/assets/commonStyles.css"></style>

<style scoped>

  .page{

    display:flex;
    flex-direction:column;
    align-items:center;
    padding-top:40px;

  }

  .enterCode{

    font-size:20px;
    cursor:pointer;
    margin-bottom:20px;

  }

</style>