<template>

<div class='page'>

  <div class='imageBlock'>

    <img class='changeProfile' src='../assets/changeProfile.svg' @click='addChanges'/>

  </div>

  <div class='inputContent'>

    <contentBlock name='Имя' v-model="name" :disabled="!profileCanBeChanged"/>

  </div>

  <div

    v-if='profileCanBeChanged==true'

    class='actionButton saveChanges'

    @click='saveChanges'

    >Сохранить изменения

  </div>

</div>

</template>


<script>

  import contentBlock from '../components/contentBlock.vue';

  import { useMainDataStore } from '@/shared/stores/mainData.js';

  import { updateMainDataOfUser } from '../api/updateMainDataOfUser.js';

  export default{

    setup(){
      
      let name=computed(()=>useMainDataStore().name);

      return{ name }

    },

    data(){
      return{

        profileCanBeChanged:false,

      }
    },


    methods:{

      addChanges(){

        this.profileCanBeChanged=true;

      },

      saveChanges(){

        //Создаем объект для отправки на сервер
        let mainDataOfUser={

          name:this.name,

        }

        //Отправляем данные на сервер
        updateMainDataOfUser(mainDataOfUser);

        this.profileCanBeChanged=false;

      }

    },

    components:{

      contentBlock

    },

  }

</script>


<style scoped>

  .page{

    height:calc(100% - 56px);
    width:100%;
    padding-top:40px;
    padding-left:10px;

    display:flex;
    flex-direction:column;
    align-items:center;
    position:relative;

  }


  .imageBlock{

    display:flex;
    justify-content:center;
    position:relative;
    user-select:none;
    margin-bottom:10px;
    //background:red;

  }


  .imageBlock>.avatar{

    height:auto;
    width:50%;
    min-width:100px;
    max-width:300px;
    border-radius:100px;

  }

  .imageBlock>.changeProfile{

    height:30px;
    width:30px;
    position:absolute;
    left:calc(50% + 33px);
    bottom:0px;
    cursor:pointer;

  }


  .inputContent{

    //background:blue;

  }
  .actionButton{

    display:flex;
    justify-content:center;
    align-items:center;
    border-radius:20px;
    height: 48px;
    width: 95%;
    margin-left:auto;
    margin-right:auto;


    font-style: normal;
    font-size: 18px;
    line-height: 22px;
    color: rgba(0, 0, 0, 0.5);
    background: #D8D5D5;
    cursor:pointer;
    user-select:none;

    font-weight: bold;
    background:#D9D9D9;


  }

  .saveChanges{

    position:absolute;
    bottom:30px;
    margin:auto;
    left:0px;
    right:0px;

  }


</style>























