
import { axios } from '@/shared/axios.js';

async function updateMainDataOfUser(mainDataOfUser){

	try{
		
		//Пытаемся отправить запрос на сервер
		let response=await axios.put('users/updateMainDataOfUser', {...mainDataOfUser});

          console.log(response);

          if(response.data.actionMessage=='mainDataOfUserWasUpdated'){

            localStorage.setItem(

              'mainDataOfUser',

              JSON.stringify(mainDataOfUser)

            );
          }

	}catch(error){

		console.log(error);

		//В случае ошибки подключения пишем, что сервер не допступен
		if(error.code=='ERR_NETWORK'){

			return {errorExist:true, actionMessage:'serverIsNotAvailable'};

		}

	}

}

export { updateMainDataOfUser };