
import { axios } from '@/shared/axios.js'

import router from '@/router'

import { authorize } from '@/shared/services/authorize.js';


async function confirmUserRegistration(data){
	
	let {email, password, confirmationCode}=data;


		
		//Пытаемся отправить запрос на сервер
		let response=await axios.post('users/confirmUserRegistration', {

			email:email,

			confirmationCode:confirmationCode,

		});

        console.log(response);
		
		//Если регистрация была произведена успешно, то авторизируемся
        if(response.data.errorExist==false && response.data.actionMessage=='registrationOfUserHasBeenConfirmed'){
			
			authorize(email, password);
		
        }
		
		return response;

	

}

export { confirmUserRegistration };